                        win_bid_publicity : name.win_bid_publicity,
                        is_end : name.is_end,
                        is_up_to_par : name.is_up_to_par,
                        remark : name.remark,
                        editting : false,
                        saving : false,

                    }
                    that.dataList.push(datasObj)
                    that.cloneDataList.push(datasObj)

                })
            },
