import Vue from 'vue'
import Router from 'vue-router'

import Login from './views/Login.vue'
import Card from './views/Card.vue'
import Channeltable from './views/ChannelTable.vue'
import Enterdepots from './views/EnterDepots.vue'
import Winbids from './views/WinBids'

Vue.use(Router)

export default new Router({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'login',
            component: Login
        },
        {
            path: '/card',
            name: 'card',
            component: Card,
            meta: {auth:true}
        },
        {
            path: '/channeltable',
            name: 'channeltable',
            component: Channeltable,
            meta: {auth:true}
        },
        {
            path: '/enterdepots',
            name: 'enterdepots',
            component: Enterdepots,
            meta: {auth:true}
        },
        {
            path: '/winbids',
            name: 'winbids',
            component: Winbids,
            meta: {auth:true}
        },
    ]
})
