<template>
    <div class="container">
        <div class="logo">
            <img src="../assets/img/LOGO.png" alt="">
        </div>
        <h3></h3>
        <Form ref="formInline" :model="formInline" :rules="ruleInline" class="form_box">
            <FormItem prop="user">
                <Input type="text" v-model="formInline.user" placeholder="">
                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                </Input>
            </FormItem>
            <FormItem prop="password">
                <Input type="password" v-model="formInline.password" placeholder="">
                    <Icon type="ios-lock-outline" slot="prepend"></Icon>
                </Input>
            </FormItem>
            <FormItem>
                <Button type="primary" @click="handleSubmit('formInline')"></Button>
            </FormItem>
        </Form>
    </div>

</template>
<script>
    export default {
        data () {
            return {
                formInline: {
                    user: 'admin',
                    password: '123456'
                },
                ruleInline: {
                    user: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', min: 6, message: '6', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit(name) {
                const that = this;
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$axios.post('./login',{
                            name: this.formInline.user,
                            password: this.formInline.password
                        }).then((res)=>{
                            // console.log(res)
                            if(res.data.status == 0){
                                that.$store.commit('set_token', res.headers.authorization)
                                that.$router.push({name: 'card'})
                            }

                            that.$Message.success(res.data.msg);
                        }).catch((err)=>{
                            console.log(err)
                        })
                    } else {
                        this.$Message.error('!');
                    }
                })
            }
        }
    }
</script>
<style lang="less" scoped>
    .container{
        width: 800px;
        text-align: center;
        margin: 60px auto;
        .logo{
            width: 200px;
            margin: 0 auto;
            img{
                width: 100%;
            }
        }
        h3{
            margin-top: 20px;
            font-weight: normal;
            font-size: 20px;
        }
        .form_box{
            width: 300px;
            margin: 50px auto;
        }
    }
</style>
