import {deleteButton, editButton, inputButton, selectButton} from './common'
import cityJson from "./city";

export default function (item,that) {
    // console.log(item)
    // $set 
    // titleHtml 
    /*if (item.titleHtml) {
        that.$set(item, 'renderHeader', function (h, params) {
            return h('span', {
                domProps: {
                    innerHTML: params.column.titleHtml
                }
            });
        });
    }*/

    let tipTime = 0;

    if($('.ivu-tooltip').css('width') != '100%'){
        tipTime = setInterval(()=>{
            $('.ivu-tooltip').css('width','100%')
        })
    }else{
        clearInterval(tipTime)
    }




    //  
    if (item.handle) {
        item.render = function (h, param) {
            var currentRow = that.cloneDataList[param.index];
            var children = [];
            item.handle.forEach(function (item) {
                if (item === 'edit') {
                    children.push(editButton(that, h, currentRow, param.index));
                } else if (item === 'delete') {
                    children.push(deleteButton(that, h, currentRow, param.index));
                }
            });
            return h('div', children);
        };
    }else if (item.addprop) {
        item.render = function (h, param) {
            var currentRow = that.cloneDataList[param.index];
            var children = [];

            // 
            if (!currentRow.editting) {
                return h('span', currentRow[item.key]);
            } else {
                // 
                //option
                item.addprop.forEach(function (item) {
                    if (item === 'input') {
                        children.push(inputButton(that, h, currentRow, param));
                    } else if (item === 'select') {
                        children.push(selectButton(that, h, currentRow, param, param.index));
                        setTimeout(() => {
                            $('.input_select').find('.ivu-select-dropdown').css({'z-index': '4'})
                                .end().find('.ivu-select-selection').css({'top': '-17px'})
                                .end().find('.ivu-select-arrow').css({'z-index': '3'})
                        })
                    }
                });
                return h('div', children);
            }
        };
    }else if (item.startEnd) {
        item.render = function (h, params) {
            var currentRow = that.cloneDataList[params.index];

            // 
            if (!currentRow.editting) {
                if (item.date) {
                    // console.log(currentRow[item.key])
                    // return h('span', that.utils.formatDate(currentRow[item.key[0]])+item.key[1]+that.utils.formatDate(currentRow[item.key[2]]))
                    return h('span', currentRow[item.key[0]]+item.key[1]+currentRow[item.key[2]])
                }

                return h('span', currentRow[item.key]);
            } else {
                // 
                // console.log(item)
                return h('Tooltip', {
                    props: {
                        content: currentRow[item.key[0]]+' '+currentRow[item.key[2]]||'()',
                        placement:'top',
                        theme:'light',
                        maxWidth:200,
                        transfer:true
                    }
                },[
                    h('DatePicker', {
                        props: {
                            type: 'daterange',
                            format:'yyyy-MM-dd',
                            clearable:false,
                            transfer:true,
                            // ******:  currentRow[params.column.key],cloneDataList
                            value: [currentRow[item.key[0]],currentRow[item.key[2]]]
                        },
                        on: {
                            'on-change': function (value) {
                                // console.log(value)
                                setTimeout(()=>{
                                    currentRow[item.key[0]] = value[0];
                                    currentRow[item.key[2]] = value[1];
                                },10)

                                // that.$set(currentRow, params.column.key, value)
                            }
                        }
                    })]);

                /*return h('DatePicker', {
                    props: {
                        type: 'daterange',
                        format:'yyyy-MM-dd',
                        clearable:false,
                        transfer:true,
                        // ******:  currentRow[params.column.key],cloneDataList
                        value: [currentRow[item.key[0]],currentRow[item.key[2]]]
                    },
                    on: {
                        'on-change': function (value) {
                            // console.log(value)
                            setTimeout(()=>{
                                currentRow[item.key[0]] = value[0];
                                currentRow[item.key[2]] = value[1];
                            },10)

                            // that.$set(currentRow, params.column.key, value)
                        }
                    }
                });*/
            }
        };
    }else if (item.editable) {
        item.render = function (h, params) {
            // console.log(params,'---')
            var currentRow = that.cloneDataList[params.index];
            // 
            if (!currentRow.editting) {
                //  (formatDate)
                if (item.date) {
                    // return h('span', that.utils.formatDate(currentRow[item.key], item.date.split('_')[1]))
                    return h('span', currentRow[item.key])
                }
                // valuelabel
                if (item.option) {
                    // object,every
                    if (typeof item.option[0] === 'object') {
                        return h('span', currentRow[item.key]);
                    }
                }

                // urla
                if (item.url) {
                    // if (typeof item.option[0] === 'object') {
                    return h('a', {
                        attrs: {
                            href: currentRow[item.key],
                            target:'_blank'
                        }
                    },'');
                    // }
                }


                return h('span', currentRow[item.key]);
            } else {
                // 

                //hover
                if (item.url){
                    return h('Tooltip', {
                        props: {
                            content: currentRow[item.key]||'()',
                            placement:'top',
                            theme:'light',
                            maxWidth:200,
                            transfer:true
                        }
                    },[
                     h('Input', {
                        props: {
                            type: item.input || 'text',
                            value: currentRow[item.key]
                        },
                        on: {
                            'on-change'(event) {
                                clearTimeout(vm.time)
                                // that.$set(currentRow, params.column.key, event.target.value)
                            },
                            'on-keyup'(event) {

                                vm.time = setTimeout(()=>{
                                    currentRow[item.key] = event.target.value
                                },2000)

                            }
                        }
                    })]);
                }else if (item.option) {//option
                    return h('Tooltip', {
                        props: {
                            content: currentRow[item.key]||'()',
                            placement:'top',
                            theme:'light',
                            maxWidth:200,
                            transfer:true
                        }
                    },[
                        h('Select', {
                            props: {
                                // ******:  currentRow[params.column.key],cloneDataList
                                value: currentRow[item.key],
                                transfer:true,
                            },
                            on: {
                                'on-change': function (value) {
                                    setTimeout(()=>{
                                        currentRow[item.key] = value
                                    },10)
                                    // console.log(value)
                                    // that.$set(currentRow, params.column.key, value)
                                }
                            }
                        }, item.option.map(function (item) {
                            return h('Option', {
                                props: {
                                    value: item.value || item,
                                    label: item.label || item
                                }
                            }, item.label || item);
                        }))]);


                    /*return h('Select', {
                        props: {
                            // ******:  currentRow[params.column.key],cloneDataList
                            value: currentRow[item.key],
                            transfer:true
                        },
                        on: {
                            'on-change': function (value) {
                                setTimeout(()=>{
                                    currentRow[item.key] = value
                                },10)
                                // console.log(value)
                                // that.$set(currentRow, params.column.key, value)
                            }
                        }
                    }, item.option.map(function (item) {
                        return h('Option', {
                            props: {
                                value: item.value || item,
                                label: item.label || item
                            }
                        }, item.label || item);
                    }));*/
                } else if (item.city) {

                    let currentCity = currentRow[item.key].replace(/\//g,',').split(",");

                    function trim(str) {
                        return str.replace(/(^\s*)|(\s*$)/g, "");
                    }

                    let cityValueArr = [];

                    //
                    cityJson.forEach(function (item111) {
                        if (currentCity.length == 3){
                            if (item111.label == trim(currentCity[0])){
                                cityValueArr[0] = item111.value
                                item111.children.forEach(function (children1) {
                                    if (children1.label== trim(currentCity[1])){
                                        cityValueArr[1] = children1.value
                                        children1.children.forEach(function (children2) {
                                            if (children2.label == trim(currentCity[2])){
                                                cityValueArr[2] = children2.value
                                            }
                                        })
                                    }
                                })
                            }
                        }else if(currentCity.length == 2){
                            if (item111.label == $.trim(currentCity[0])){
                                cityValueArr[0] = item111.value
                                item111.children.forEach(function (children1) {
                                    if (children1.label== $.trim(currentCity[1])){
                                        cityValueArr[1] = children1.value
                                    }
                                })
                            }
                        }

                    })

                    // console.log(cityValueArr)

                    // 
                    /*if (cityValueArr.length == 0){
                        cityValueArr = item.value;
                    }*/

                    return h('Tooltip', {
                        props: {
                            content: currentRow[item.key]||'()',
                            placement:'top',
                            theme:'light',
                            maxWidth:200,
                            transfer:true
                        }
                    },[
                        h('Cascader', {
                                props: {
                                    // ******:  currentRow[params.column.key],cloneDataList
                                    // data: currentRow[params.column.key],
                                    data: item.city,
                                    placeholder:' ',
                                    value: cityValueArr,
                                    clearable: false,
                                    trigger: 'hover',
                                    transfer:true
                                },
                                on: {
                                    'on-change': function (value, selectedData) {
                                        // console.log(currentRow)
                                        setTimeout(()=>{
                                            if (selectedData[2]) {
                                                currentRow[item.key] = selectedData[0].label + '/' + selectedData[1].label + '/' + selectedData[2].label;
                                                // console.log(selectedData[0].label+'/'+selectedData[1].label+'/'+selectedData[2].label)
                                            } else {
                                                currentRow[item.key] = selectedData[0].label + '/' + selectedData[1].label;
                                                // console.log(selectedData[0].label+' / '+selectedData[1].label)
                                            }
                                            // item.value = value;
                                            currentRow[item.value] = value;
                                        },10)

                                        // console.log(value)

                                        // that.$set(currentRow, params.column.key, value)
                                    }
                                }
                            }
                            /* , item.option.map(function(item) {
                               return h('Option', {
                                 props: {
                                   value: item.value || item,
                                   label: item.label || item,
                                   children: item.children || item
                                 }
                               }, item.label || item);
                             })*/
                        )]);

                    /*return h('Cascader', {
                            props: {
                                // ******:  currentRow[params.column.key],cloneDataList
                                // data: currentRow[params.column.key],
                                data: item.city,
                                placeholder:' ',
                                value: cityValueArr,
                                clearable: false,
                                trigger: 'hover',
                                transfer:true
                            },
                            on: {
                                'on-change': function (value, selectedData) {
                                    // console.log(currentRow)
                                    setTimeout(()=>{
                                        if (selectedData[2]) {
                                            currentRow[item.key] = selectedData[0].label + '/' + selectedData[1].label + '/' + selectedData[2].label;
                                            // console.log(selectedData[0].label+'/'+selectedData[1].label+'/'+selectedData[2].label)
                                        } else {
                                            currentRow[item.key] = selectedData[0].label + '/' + selectedData[1].label;
                                            // console.log(selectedData[0].label+' / '+selectedData[1].label)
                                        }
                                        // item.value = value;
                                        currentRow[item.value] = value;
                                    },10)

                                    // console.log(value)

                                    // that.$set(currentRow, params.column.key, value)
                                }
                            }
                        }
                        /!* , item.option.map(function(item) {
                           return h('Option', {
                             props: {
                               value: item.value || item,
                               label: item.label || item,
                               children: item.children || item
                             }
                           }, item.label || item);
                         })*!/
                    );*/
                    // })


                } else if (item.date) {

                    return h('Tooltip', {
                        props: {
                            content: currentRow[item.key]||'()',
                            placement:'top',
                            theme:'light',
                            maxWidth:200,
                            transfer:true
                        }
                    },[
                        h('DatePicker', {
                            props: {
                                // type: item.date.split('_')[0] || 'date',
                                type: 'date',
                                clearable: false,
                                value: currentRow[params.column.key],
                                transfer:true
                            },
                            on: {
                                'on-change': function (value) {
                                    setTimeout(()=>{
                                        currentRow[item.key] = value
                                    },10)

                                    // that.$set(currentRow, params.column.key, value)
                                }
                            }
                        })]);


                    //date
                    /*return h('DatePicker', {
                        props: {
                            // type: item.date.split('_')[0] || 'date',
                            type: 'date',
                            clearable: false,
                            value: currentRow[params.column.key],
                            transfer:true
                        },
                        on: {
                            'on-change': function (value) {
                                setTimeout(()=>{
                                    currentRow[item.key] = value
                                },10)

                                // that.$set(currentRow, params.column.key, value)
                            }
                        }
                    });*/
                } else {

                    return h('Tooltip', {
                        props: {
                            content: currentRow[item.key]||'()',
                            placement:'top',
                            theme:'light',
                            maxWidth:200,
                            transfer:true
                        }
                    },[
                        h('Input', {
                            props: {
                                type: item.input || 'text',
                                // rowsinput textarea
                                // rows: 3,
                                value: currentRow[item.key]
                            },
                            on: {
                                'on-change'(event) {
                                    clearTimeout(vm.time)
                                    // that.$set(currentRow, params.column.key, event.target.value)
                                },
                                'on-keyup'(event) {

                                    vm.time = setTimeout(()=>{
                                        currentRow[item.key] = event.target.value
                                    },2000)

                                }
                            }
                        })]);

                    // input
                    /*return h('Input', {
                        props: {
                            type: item.input || 'text',
                            // rowsinput textarea
                            // rows: 3,
                            value: currentRow[item.key]
                        },
                        on: {
                            'on-change'(event) {
                                clearTimeout(vm.time)
                                // that.$set(currentRow, params.column.key, event.target.value)
                            },
                            'on-keyup'(event) {

                                vm.time = setTimeout(()=>{
                                    currentRow[item.key] = event.target.value
                                },2000)

                            }
                        }
                    });*/
                }
            }
        };
    }

}