//axios
import axios from 'axios'


let baseUrl = "";   //url
switch (process.env.NODE_ENV) {
    case 'development':
        baseUrl = "/api"  //url
        break
    case 'production':
        baseUrl = "/b_infor/public/api"   //url
        // baseUrl = "https://121.43.191.71/b_infor/public/api"   //url
        break
}


// baseURL
axios.defaults.withCredentials = true;
axios.defaults.baseURL = baseUrl;
// axios.defaults.baseURL = ''
//
axios.defaults.headers = {
    'X-Requested-With': 'XMLHttpRequest'
}
// 
axios.defaults.timeout = 10000

//
axios.interceptors.request.use(config => {

    config.headers = {
        'Content-Type': 'application/json'
    }
    // token
    //tokenheadertoken
    if(localStorage.getItem('token')){
        config.headers.Authorization = localStorage.getItem('token')
    }

    return config
}, error => {
    return Promise.reject(error)
})

//
axios.interceptors.response.use(response => {
    // code

    if (response.data.status == 1){//token

        localStorage.remove('token');

        Vue.prototype.$Message.warning({
            content: '',
            duration: 5
        })

        router.push({ name: '/login' })

    } else if (response.headers.authorization){ // tokentoken

        localStorage.setItem('token',  response.headers.authorization) // token()
        axios.defaults.headers.Authorization = response.headers.authorization
    }

    //Message.error(err.message)

    return response
}, error => {
    if(error && err.response) {
        switch(err.response.status) {
            case 400:
                err.message = ''
                break;
            case 401:
                err.message = ''
                break;
            case 403:
                err.message = ''
                break;
            case 404:
                err.message = ','
                break;
            case 405:
                err.message = ''
                break;
            case 408:
                err.message = ''
                break;
            case 500:
                err.message = ''
                break;
            case 501:
                err.message = ''
                break;
            case 502:
                err.message = ''
                break;
            case 503:
                err.message = ''
                break;
            case 504:
                err.message = ''
                break;
            case 505:
                err.message = 'http'
                break;
            default:
                err.message = `${err.response.status}`
        }
    } else {
        err.message = ""
    }
    // Message.error(err.message)

    Vue.prototype.$Message.error({
        content: err.message,
        duration: 5
    })

    return Promise.resolve(error.response)
})

export default {
    // get
    get (url, param = {}) {
        return new Promise((resolve, reject) => {
            axios.get(url,{params:param})
                .then(res => {
                    resolve(res)
                }, err => {
                    reject(err)
                })
        })
    },
    // post
    post (url, param = {}) {
        return new Promise((resolve, reject) => {
            axios.post(
                url,
                param
            ).then(res => {
                resolve(res)
            }, err => {
                reject(err)
            })
        })
    },
    // put
    put (url, param = {}) {
        return new Promise((resolve, reject) => {
            axios.put(url, param)
                .then(response => {
                    resolve(response)
                }, err => {
                    reject(err)
                })
        })
    },
    // delete
    delete (url,param = {}){
        return new Promise((resolve, reject) => {
            axios.delete(url, param)
                .then(response => {
                    resolve(response)
                }, err => {
                    reject(err)
                })
        })
    }
}
// es6

